}

.hero-section-text {
  color: white;
  position: relative;
  top: 50%;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 20px;
}

.logo-link {
  padding: 20px;
  /* Set the initial color of the path */
  /* Change the color of the path when hovering over the link */
}
